@mixin border-define($top, $right, $bottom, $left, $color: black, $style: solid) {
    $min: 1px;
    $topx: $top * $min;
    $rightx: $right * $min;
    $bottomx: $bottom * $min;
    $leftx: $left * $min;
    border-top: $topx solid $color;
    border-right: $rightx solid $color;
    border-bottom: $bottomx solid $color;
    border-left: $leftx solid $color;
    border-style: $style;
}
svg {
    position: absolute;
    top: 1em;
    padding-left: .4em;
}
/* border box by Paul Irish 
http://www.paulirish.com/2012/box-sizing-border-box-ftw/ */

html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

