/* this is setting up the transparent background image in the header */
header {
    height: 10em;
    width: 100%;
    position: relative;
    letter-spacing: .4em;
    border-bottom: 1px solid black;
    z-index: 0;
    overflow: hidden;
}

header:after {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: url("../img/background2x_1200.png") no-repeat;
    opacity: 0.6;
    z-index: -3;
    display: block;
    background-position: 50% 50%;
    background-size: cover;
}
