/* when images on the page are hovered, the bordiar radius changes and filters are used to create a negative photo effect */
/* at the same time, the following text in the associated paragraph will be drop shadowed and skewed, via transforms and filters */
img {
  transition: border 2s;
  transition: border-radius 2s;
  position:relative;
  padding: 1em;
  border: 10px solid black;
  padding:0;
}
img:hover {
  border: 10px solid #f89921;
  border-radius: 100px;
  filter: contrast(200%);
  filter: invert(100%);
}

figure ~ div p {
    transition: background 4s; 
}
figure:hover ~ div p {
  background: darken($background-heading, 10%);
  filter: drop-shadow(15px 15px 15px black);
  transform: skewX(-5deg);
  border: 1px solid black;
}

